import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { alert_toast, base_url } from './helper';

export default function Fee_rules(props) {
  const [, forceUpdate] = useState();

  useEffect(() => {
    logout();
  }, [forceUpdate])


  const logout = () => {
    axios.get(base_url("employees/logout")).then(res => {
      if (res.status === 200) {
        localStorage.removeItem('ghtw');
        localStorage.removeItem('hgfs');
        localStorage.removeItem('gfhf');
        localStorage.removeItem('bcvb');
        localStorage.removeItem('fddg');
        setTimeout(function () {
          props.history.push({
            pathname: '/login',
            state: { logout: true }
          });
          alert_toast("sukses", "Success logout this session");
        }, 1000)
      }
    }).catch(err => props.history.push('/login'))
  }
  return (
    <>
      pleas wait...
    </>
  );
} 